<template>
  <div class="title" v-on:wheel.passive="wheelson">
    <v-touch @swipeup="switcherResume">
      <vue-particles
        color="#000000"
        :particleOpacity="1.0"
        :particlesNumber="55"
        shapeType="circle"
        :particleSize="7"
        linesColor="#FF85AC"
        :linesWidth="2"
        :lineLinked="true"
        :lineOpacity="1.0"
        :linesDistance="140"
        :moveSpeed="0.7"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="true"
        clickMode="push"
      ></vue-particles>
      <div id="text">
        <div id="first">Vishnu</div>
        <div id="last">Rengaraj</div>
        <div id="subtitle">{{ whois[0] }}</div>
      </div>

      <button id="testButton" v-on:click="clear">Clear</button>

      <button id="learn" v-on:click="switcherResume">
        <div class="arrow">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </button>
      <div id="resume1" v-bind:class="{ resume1_show: isActiveResume }"></div>
      <div id="resume2" v-bind:class="{ resume2_show: isActiveResume }"></div>
      <div id="resume3" v-bind:class="{ resume3_show: isActiveResume }"></div>
      <div
        id="resumeFinal"
        v-bind:class="{ resumeFinal_show: isActiveResume }"
        v-scroll="onScroll"
      >
        <Resume />
      </div>

    </v-touch>
  </div>
</template>

<script>
import Resume from "./Resume.vue";
/*import Project from "./Project.vue";*/

export default {
  name: "Title",
  components: {
    Resume,
    /*Project,*/
  },

  data() {
    return {
      isActiveResume: false,
      position: { scrollTop: 0, scrollLeft: 0 },
      whois: [
        "Mechanical Engineer",
        "Computer Scientist",
        "Graduate Student",
        "Designer",
        "Stress Analyst",
        "Banana Lover",
      ],
    };
  },
  mounted: function () {
    window.setInterval(() => {
      this.pollPerson();
    }, 2700);
  },
  computed: {},

  methods: {
    onScroll: function (e, position) {
      this.position = position;
      console.log(this.position.scrollTop);
    },

    clear: function () {
      this.isActiveResume = false;
      this.isActiveProject = false;
    },

    wheelson: function (event) {
      if (event.deltaY > 0) {
        this.isActiveResume = true;
      }

      if (event.deltaY < 0) {
        if (this.position.scrollTop == 0) {
          this.isActiveResume = false;
        }
      }
    },

    switcherResume: function () {
      if (this.isActiveResume == false) {
        this.isActiveResume = true;
      }
      console.log(this.isActiveResume);
    },

    switcherResumeOpp: function () {
     
        this.isActiveResume = false;
      
      console.log(this.isActiveResume);
    },
    /*
    switcherProject: function () {
      if (this.isActiveResume == false) {
        this.isActiveProject = true;
      }
      console.log(this.isActiveProject);
    },

    switcherProjectOpp: function () {
      if (this.isActiveResume == false) {
        this.isActiveProject = false;
      }
      console.log(this.isActiveProject);
    },
*/
    pollPerson() {
      const first = this.whois.shift();
      this.whois = this.whois.concat(first);
    },
  },
};
</script>

<style scoped>
.resume1_show,
.resume2_show,
.resume3_show,
.resumeFinal_show {
  height: 100% !important;
}

.resumeFinal_show,
.projectFinal_show {
  overflow-y: scroll !important;
}

.project1_show,
.project2_show,
.project3_show,
.projectFinal_show {
  width: 100% !important;
}

#learn {
  background-color: transparent;
  border: none;
  box-shadow: transparent;
  position: absolute;
  left: 50%;
  transform: translateX(-50%) !important;

  bottom: 12vw;
  z-index: 2;
}
.arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.arrow span {
  display: block;
  width: 4vw;
  height: 4vw;
  border-bottom: 3px solid #fafafa;
  border-right: 3px solid #fafafa;
  transform: rotate(45deg);
  margin: -10px;
  animation: animate 2s infinite;
}
.arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.arrow span:nth-child(3) {
  animation-delay: -0.4s;
}
@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-20px, -20px);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(20px, 20px);
  }
}

#pull {
  background-color: #85ffd8;
  position: absolute;
  width: 10%;
  left: 50%;
  bottom: 0%;

  z-index: 7;
  height: 6%;
  transition: all 800ms ease-in-out; /*cubic-bezier(0.075, 0.82, 0.165, 1);*/
}

#testButton {
  position: absolute;
  right: 5%;
  top: 5%;
  z-index: 99;
}

#resume1,
#resume2,
#resume3,
#resumeFinal {
  overflow-x: hidden;
  overflow-y: hidden;
  position: absolute;
  z-index: 7;
  width: 100%;
  height: 0%;
  left: 0;
  bottom: 0;
  background-color: #3b434d;
  transition: all 1000ms ease-in-out; /*cubic-bezier(0.075, 0.82, 0.165, 1);*/
}

#project1,
#project2,
#project3,
#projectFinal {
  overflow-x: hidden;
  overflow-y: hidden;
  position: absolute;
  z-index: 7;
  width: 0;
  height: 100%;
  right: 0%;
  bottom: 0;
  background-color: #3b434d;
  transition: all 700ms ease-out; /*cubic-bezier(0.075, 0.82, 0.165, 1);*/
}

#resume2,
#project2 {
  z-index: 8;
  /*height: 4%;*/
  background-color: #737980;
  transition-delay: 150ms;
}

#resume3,
#project3 {
  z-index: 9;
  /*height: 2%;*/
  background-color: #abaeb3;
  transition-delay: 300ms;
}

#resumeFinal,
#projectFinal {
  z-index: 10;
  /*height: 0%;*/
  background-color: #fafafa;
  transition-delay: 450ms;
}

#particles-js {
  background-color: #1f2833;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#text {
  position: absolute;
  top: 18%;
  left: 15%;
  height: 40vh;
  z-index: 2;
}

#first,
#last,
#subtitle {
  text-align: left;
  letter-spacing: 7px;
  font-size: 9vw;
  color: #fafafa;
  padding: 5vw;
  padding-left: 0;
  font-weight: 600;
  display: block;
  z-index: 2;
  /*
  background-color: black;*/
}

#subtitle {
  font-size: 4.5vw;
  padding: 2.5vw;
  padding-left: 0;
  font-weight: 400;
}

@media screen and (min-width: 1000px) {
  #first,
  #last {
    font-size: 90px;
    padding: 50px;
    padding-left: 0;
  }

  #subtitle {
    font-size: 45px;
    padding: 25px;
    padding-left: 0;
  }

  #learn {
    bottom: 120px;
  }

  .arrow span {
    width: 30px;
    height: 30px;
  }
}
</style>
